import { Component, AfterViewInit } from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements AfterViewInit{
  scrollInstances: number =  0;
  scrollIndicator: HTMLElement;

  scrollSubscription: Subscription;
  
  ngAfterViewInit(): void {
    this.scrollIndicator = <HTMLElement>document.getElementsByClassName("scrollIndicator")[0];
    const scroll$ = fromEvent(document.querySelector('.parallax'), 'scroll');

    this.scrollSubscription =  scroll$.subscribe(this.handleScroll.bind(this));
  }

  handleScroll() {
    this.scrollInstances++;

    if(this.scrollInstances > 15) {
      this.scrollIndicator.style.display = "none";
      this.scrollSubscription.unsubscribe();
    }
  }
}
